import Home from '../Home/Home'

import PersonReserve from '../Person/PersonReserve'
import PersonSuccess from '../Person/PersonSuccess'

import TeamReserve from '../Team/TeamReserve'
import TeamSuccess from '../Team/TeamSuccess'

import Search from '../SearchReserve/Search'
import Result from '../SearchReserve/Result'

import ReturnResult from '../ReturnReserve/ReturnResult'
import ReturnSearch from '../ReturnReserve/ReturnSearch'
const path = window._config.path;
const routes = [
    //主页模块
    {
        path: path + '/home',
        component: Home,
    },
    //个人预约模块
    {
        path: path + '/person',
        component: PersonReserve,
    },
    {
        path: path + '/person-success',
        component: PersonSuccess,
    },
    //团队预约模块
    {
        path: path + '/team',
        component: TeamReserve,
    },
    {
        path: path + '/team-success',
        component: TeamSuccess,
    },
    //预约查询模块
    {
        path: path + '/search',
        component: Search,
    },
    {
        path: path + '/result',
        component: Result,
    },
    //预约退订模块
    {
        path: path + '/return-search',
        component: ReturnSearch,
    },
    {
        path: path + '/return-result',
        component: ReturnResult,
    },
]

export default routes;


