import React, { Component } from 'react';
import {Flex, List, InputItem, Picker, Toast} from 'antd-mobile';
import {Helmet} from "react-helmet"
import {withRouter } from 'react-router-dom'
import {reserveClass} from '../DataForm'
import '../common-search.css'

const inputReg = new RegExp("^[0-9a-zA-Z]+$");
//Module-->预约搜索的搜索界面
class Search extends Component{

    //State数据初始化
    constructor(props) {
        super(props);
        this.state = {
            type: [0],
            num: ''
        };
    } 

    //处理查询操作
    handleSearch = () => {
        //1. 检查内容是否完整
        if (null == this.state.type){
            Toast.fail("请选择预约号类型", 1.5);
            return;
        }
        if ('' === this.state.num){
            Toast.fail("请输入预约号/证件号", 1.5);
            return;
        }
        //2. 跳转至查询结果界面
        this.props.history.push(
            `${window._config.path}/result`,
            {
                type: this.state.type[0],
                num: this.state.num.toUpperCase()
            },
        );
    }

    render(){
        return (
            <div className='search-content'>
            {/*设置头*/}
            <Helmet><title>预约查询</title></Helmet>

            <div className="search-input-group">
                {/*预约号类型*/}
                <Flex wrap='wrap' className="search-input-item">
                    <div className="search-input-item-title">预约号类型</div>
                    <div style={{flex:'1'}}>
                        <Picker 
                            data={reserveClass}  
                            cols={1} 
                            value={this.state.type}
                            onOk={value => {
                                this.setState({
                                    type: value
                                });
                            }}>
                            <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                    </div>
                </Flex>
                {/*预约号/证件号*/}
                <Flex wrap='wrap' className="search-input-item" style={{marginTop:'3.8vw'}}>
                    <div className="search-input-item-title">预约号/证件号</div>
                    <div style={{flex:'1'}}>
                        <InputItem 
                            placeholder="请填写"
                            maxLength={18}
                            value={this.state.num}
                            onChange={value => {
                                if (inputReg.test(value) || '' === value){
                                    this.setState({
                                        num: value
                                    })
                                }
                            }}/>
                    </div>
                </Flex>
                {/*查询按钮*/}
                <div className="search-confirm-btn" onClick={this.handleSearch}>查询</div>
            </div>
            
            {/*底部logo*/}
            <div className="cz-logo-relative" style={{marginTop:'52vw'}}></div>
            </div>
        );
    }
}

export default withRouter(Search)


