import React, { Component } from 'react'
import {Flex} from 'antd-mobile'
import {Link} from 'react-router-dom'
import {Helmet} from "react-helmet"
import './style.css'

//Module-->参观预约
const path = window._config.path;
class Home extends Component{

    render(){
        return (
            <div style={{background:'#ffffff'}}>
                {/*设置头*/}
                <Helmet><title>参观预约</title></Helmet>
                <div className='home-content'>
                    {/*预约文字介绍内容*/}
                    <div className="home-intro-area">
                        <div style={{fontSize: '4.5vw'}}>观众朋友：</div>
                        <div style={{textIndent: '7.8vw'}}>您好，欢迎参观常州博物馆！</div>
                        <div style={{textIndent: '7.8vw'}}>我馆开放时间为每日9:00-17:00（16:00停止入场)，周一闭馆。</div>
                        <div style={{textIndent: '7.8vw'}}>为确保文物安全，保持良好的参观环境，常州博物馆将对观众流量实施限量、有序控制，每天的参观人数控制在 8000人次以下（其中含预约观众）。</div>
                        <div style={{textIndent: '7.8vw'}}>为维持良好的参观秩序，观众当天现场分时段免费领取参观券，依次排队入馆参观。团体参观可通过电话形式提前预约，参观当日经工作人员核准后，优先入馆。</div>
                        <div style={{textIndent: '7.8vw'}}>预约电话：</div>
                        <div style={{textIndent: '7.8vw'}}>0519-85165089或85165080转8078分机。</div>
                    </div>

                    {/*底部功能按钮组*/}
                    <Flex wrap="wrap" className="home-btn-group">

                        <Link to={`${path}/person`}>
                        <div className="home-btn-item">
                            <div>个人预约</div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>

                        <Link to={`${path}/team`}>
                        <div className="home-btn-item">
                            <div>团队预约</div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>

                        <Link to={`${path}/search`}>
                        <div className="home-btn-item">
                            <div>预约查询</div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>

                        <Link to={`${path}/return-search`}>
                        <div className="home-btn-item">
                            <div>预约退订</div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Home




