import React, { Component } from 'react'
import {Flex} from 'antd-mobile'
import {cardClass, provList} from '../DataForm'

class InfoItem extends Component{

    /*{
        ticketName: this.props.mTicketClass,
        areaType: null,
        cardType: [0],
        cardNum: '',
        name: ''
    }*/
    render(){

        //FIXME: 获取证件名
        let cardName = (cards, cardType) => {
            for (var i = 0; i < cards.length; i++){
                if (cards[i].value == cardType)
                    return cards[i].label
            }
            return '[NULL]'
        }
        //FIXME: 获取省份名
        let provName = (provs, areaType) => {
            for (var i = 0; i < provs.length; i++){
                if (provs[i].value == areaType)
                    return provs[i].label
            }
            return '[NULL]'
        }

        return (
            <Flex className='step3-info'>
                <div className='step3-info-long'>
                    {cardName(this.props.cardClass, this.props.infos.cardType[0])}：{this.props.infos.cardNum}
                </div>
                <div className='step3-info-short'>
                    {provName(provList, this.props.infos.areaType[0])}
                </div>
                <div className='step3-info-short'>{this.props.infos.name}</div>
            </Flex>
        );
    }
}

export default InfoItem


