import React, { Component } from 'react';
import {Flex} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'

const personSuccess = '/static/visit/cz_person_success.png'
class PersonSuccess extends Component{

    //处理查看预约(预约类型+预约号)
    handleSearch = () => {
        this.props.history.push(
            `${window._config.path}/result`,
            {
                type: 0,
                num: this.props.location.state.num
            },
        );
    }

    render(){

        //背景
        let bgStyle = {
            width: '100%',
            height: '100vh',
            background:`url(${personSuccess}) top  no-repeat / contain`,
        };

        return (
            <div style={{background:'#ffffff'}}>
            {/*设置头*/}
            <Helmet><title>预约成功</title></Helmet>
            <div style={bgStyle}>

                <div className='success-bottom'>
                    {/*TODO: 动态显示预约号*/}
                    <div className='success-number'>
                    你的预约号: {this.props.location.state.num}
                    </div>
                    {/*提示内容框*/}
                    <div className='success-card'>
                        <div className='success-card-title'>温馨提示:</div>
                        {/*动态显示预约时间*/}
                        <div className='success-card-detail'>
                        请牢记您的预约号并于{this.props.location.state.date}前去博物馆取票，以免错过参观！
                        </div>
                        <Flex>
                            <img className='success-wechat' src='/static/visit/cz_wechat.png'/>
                            <div className='success-card-hint'>长按二维码关注常州博物馆公众号及时查看预约状态</div>
                        </Flex>
                    </div>
                    {/*底部按钮*/}
                    <div className='success-btn' onClick={this.handleSearch}>查看预约</div>
                </div>

            </div>
            </div>
        );
    }
}

export default withRouter(PersonSuccess)  


