import React, { Component } from 'react'

//Widgets-->单个日期标签
class DateTag extends Component{

    //处理未被选中/且可以选择的tag
    handleTagSelect = () => {
        if (0 == this.props.tag.close && 0 == this.props.tag.selected)
            this.props.handleTagSelect(this.props.index);
    }

    render(){
        //处理要使用的className
        let usedClassName = (selectable, selected) => {
            if (0 == selectable && 0 == selected)
                return 'step1-date-tag-unselected'
            if (0 == selectable && 1 == selected)
                return 'step1-date-tag-selected'
            if (1 == selectable)
                return 'step1-date-tag-unselectable'
        }

        return (
            <div 
                className={usedClassName(this.props.tag.close, this.props.tag.selected)} 
                onClick={this.handleTagSelect}>
                {this.props.tag.date}
            </div>
        );
    }
}

export default DateTag;


