import React, { Component } from 'react';
import {List} from 'antd-mobile';
import { getSimpleDate } from '../DataForm';

//Widgets-->显示团队信息的小组件
class TeamItem extends Component{

    /*{
        "num": "34",
        "status": "0",
        "ordernum": "1515403714506",
        "organizer": "测试",
        "name": "测试",
        "visitTime": "2018-01-09 11:00"
    }*/
    render(){

        //获取订单状态信息
        let statusInfo = (status) => {
            console.log(status);
            switch(status){
                case "0": return "待审核"
                case "1": return "审核通过"
                case "2": return "审核不通过"
                case "4": return "已登记"
            }
        }

        return (
            <div className="card-outer">
                <List className="card-inner">
                    <List.Item extra={this.props.datas.name}>团体名称</List.Item>
                    <List.Item extra={this.props.datas.organizer}>组织人</List.Item>
                    <List.Item extra={this.props.datas.num}>参观人数</List.Item>
                    <List.Item extra={this.props.datas.visitTime}>参馆时间</List.Item>
                    <List.Item extra={this.props.datas.ordernum}>预约号</List.Item>
                    <List.Item extra={statusInfo(this.props.datas.status)}>状态</List.Item>
                </List>
            </div>
        );
    }
}

export default TeamItem


