import React, { Component } from 'react'
import {Flex, Toast} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter } from 'react-router-dom'
import PersonItem from '../SearchReserve/PersonItem'
import {api_select_return, api_delete_ticket} from '../Api'
import './style-return-result.css'
import { getParams } from '../DataForm';

class ReturnResult extends Component{

    //state存放个人预约数据
    constructor(props){
        super(props);
        this.state = {
            data: []
        }
    }
    
    //获取查询退订
    componentDidMount(){
        //console.log(getParams(api_select_return, this.props.location.state));
        fetch(api_select_return, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.props.location.state)
        })
            .then((response) => {
                if (response.status >= 200
                    && response.status < 300)
                    return response.json()
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data
            })
            .then((data) => {
                //console.log(data)
                if (0 === data.length)
                    Toast.fail("找不到预约，请填写正确信息"
                                , 1.5
                                , () => {
                                    this.props.history.go(-1);
                                });
                this.setState({
                    data: data
                });
            })
    }

    //处理退票事件
    handleReturn = () => {
        fetch(api_delete_ticket,  {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                ids: this.state.data[0].id
            })
        })
            .then((response) => {
                if (response.status >= 200 
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                //处理成功事件
                console.log(json);
                if (1 == json.code){
                    Toast.success ("退票成功", 1.5, () => {
                        this.props.history.go(-1);
                    });
                }
            })
    }

    render(){

        //判断是否要生成个人预约和按钮
        let card = 0 === this.state.data.length? null : <PersonItem datas={this.state.data[0]}/> 
        let btn = 0 === this.state.data.length? null : <div className="return-result-btn" onClick={this.handleReturn}>退票</div>

        return (
            <div style={{background: "#ffffff"}}>
                {/*设置头*/}
                <Helmet><title>预约查询</title></Helmet>
                <Flex direction='column' className='card-list'>
                    {
                        card
                    }
                </Flex>
                {
                    btn
                }
                {/*底部图标*/}
                <div className='cz-logo'></div>
            </div>
        );
    }
}

export default withRouter(ReturnResult) 


