import React, { Component } from 'react'
import {Flex, Toast} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'
import PersonItem from './PersonItem'
import TeamItem from './TeamItem'
import {getParams} from '../DataForm';
import {api_select_reserve} from '../Api'

//Module-->预约搜索的结果界面
class Result extends Component{

    //初始化State
    constructor(props){
        super(props);
        this.state = {
            list: []
        }
    }

    //获取数据
    componentDidMount(){
        //TODO: 如何处理错误数据
        // console.log(getParams(get_reserve_list, this.props.location.state));
        fetch(api_select_reserve, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.props.location.state)
        })
            .then((response) => {
                if (response.status >= 200
                    && response.status < 300)
                    return response.json()
            })
            .then((json) => {
                //console.log(json);
                if (1 == json.code)
                    return json.data
            })
            .then((data) => {
                if (0 === data.length){
                    Toast.fail("找不到预约，请填写正确信息"
                                , 1.5, ()=>{
                                    this.props.history.go(-1);
                                });
                } else {
                    this.setState({
                        list: data
                    })
                } 
            })
    }

    render(){

        //结果列表
        let items = this.state.list.map((datas, index) => {
            if (0 === this.props.location.state.type)
                return <PersonItem 
                            key={index} 
                            datas={datas}/>;
            else if (1 === this.props.location.state.type)
                return <TeamItem 
                            key={index} 
                            datas={datas}/>;
        });

        return (
            <div style={{background:'#ffffff'}}>
                {/*设置头*/}
                <Helmet><title>预约查询</title></Helmet>
                {/*结果卡片展示*/}
                <Flex direction='column' className='card-list'>
                    {
                        items
                    }
                </Flex>
                {/*底部展示图标*/}
                <div className='cz-logo'></div>
            </div>
        );
    }
}

export default withRouter(Result) 


