import React, { Component } from 'react'
import {Flex} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import {api_insert_person} from '../Api'
import InfoItem from './InfoItem'
import './style-step-3.css'

//Step-->个人预约Step3
class Step3 extends Component{

    //处理上一步
    toPrevStep = () => {
        this.props.toPrevStep(3);
    }

    //处理确认预约
    toConfirm = () => {
        //将mInputs中数组转为对象
        let mInputs = JSON.parse(JSON.stringify(this.props.moduleData.mInputs));
        for (let i = 0; i < mInputs.length; i++){
            mInputs[i].areaType = mInputs[i].areaType[0];
            mInputs[i].cardType = mInputs[i].cardType[0];
            //身份证由于有x/X，因此都转大写发送
            mInputs[i].cardNum = mInputs[i].cardNum.toUpperCase();
        }
        //联机保存个人预约信息
        fetch(api_insert_person, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    date: this.props.moduleData.mDate,
                    ticketId: this.props.moduleData.mClass[0],
                    data: mInputs
                })
            })
                .then((response) => {
                    if (response.status >= 200 
                        && response.status < 300)
                        return response.json();
                })
                .then((json) => {
                    //console.log(json);
                    //返回预约号
                    if (1 == json.code)
                        return json.data
                })
                .then((data) => {
                    //成功得到预约号后，跳转至PersonSuccess
                    this.props.history.replace(
                        `${window._config.path}/person-success`,
                        {
                            date: this.props.moduleData.mDate,
                            num: data,
                        }
                    );
                })
    }

    render(){

        //信息列表
        let infoList = this.props.moduleData.mInputs.map((infos, index) => {
            return <InfoItem 
                        key = {index} 
                        infos = {infos}
                        cardClass = {this.props.moduleData.mCardClass}/>;
        });

        return (
            <div style={{background: '#ffffff'}}>
                {/*入馆时间*/}
                <div className='step3-date'>入馆时间：{this.props.moduleData.mDate}</div>
                {/*信息标题*/}
                <Flex className='step3-title'>
                    <div className='step3-title-long'>证件信息</div>
                    <div className='step3-title-short'>省份</div>
                    <div className='step3-title-short'>姓名</div>
                </Flex>
                {/*填写的信息列表*/}
                <Flex direction='column'>
                    {
                        infoList
                    }
                </Flex>
                {/*“上一步”和“确认预约”按钮*/}
                <Flex className='step3-bottom'>
                    <div className='step3-bottom-back' onClick={this.toPrevStep}>上一步</div>
                    <div className='step3-bottom-confirm' onClick={this.toConfirm}>确认预约</div>
                </Flex>
            </div>
        );
    }
}

export default withRouter(Step3) 


