import React, { Component } from 'react';
import {Flex, InputItem, Picker, List, Toast} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'
import {api_select_cards} from '../Api'
import {validateIdCard} from '../DataForm'
import '../common-search.css'

const inputReg = new RegExp("^[0-9a-zA-Z]+$");
//Module-->预约退订的搜索模块
class ReturnSearch extends Component{

    //State数据初始化
    constructor(props) {
        super(props);
        this.state = {
            paperType: null,
            paperNum: '',
            orderNum: '',
            mCards: []
        };
    } 

    //获取证件类型列表
    componentDidMount(){
        fetch(api_select_cards, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then((response) => {
                if (response.status >= 200
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data;
            })
            .then((data) => {
                let cards = [];
                for (var i = 0; i < data.length; i++)
                    cards.push({
                        label: data[i].text,
                        value: data[i].value
                    });
                this.setState({
                    mCards: cards
                });
            })
            .catch((error) => {
                //TODO: 如何处理错误数据
                console.log("error-->"+error);
            });
    }

    //处理查询事件
    handleSearch = () => {

        //1. 先检查输入内容是否完整
        if (null == this.state.paperType){
            Toast.fail("请选择证件类型", 1.5);
            return;
        }
        if ('' === this.state.paperNum){
            Toast.fail("请填写证件号码", 1.5);
            return;
        }
        if ('' === this.state.orderNum){
            Toast.fail("请填写预约号码", 1.5);
            return;
        }
        //FIXME: 2. 目前仅对身份证处理，并且默认0 == value是身份证
        // for (let i = 0; i < this.state.mCards.length; i++){
        //     if (this.state.mCards[i].value === this.state.paperType[0])
        //         console.log(this.state.mCards[i].label);
        // }
        if (0 == this.state.paperType[0] && !validateIdCard(this.state.paperNum)){
            Toast.fail("请输入正确的身份证号", 1.5);
            return false;
        }
        //3. 填写无误，则跳转至结果界面
        this.props.history.push(
            `${window._config.path}/return-result`,
            {
                paperType: this.state.paperType[0],
                paperNum: this.state.paperNum.toUpperCase(),
                orderNum: this.state.orderNum
            },
        );
    }

    render(){
        return (
            <div className="search-content">
                {/*设置头*/}
                <Helmet><title>预约退订</title></Helmet>
                <div className='search-input-group'>
                    
                    {/*证件类型*/}
                    <Flex wrap='wrap' className="search-input-item">
                        <div className="search-input-item-title" style={{width: '34vw'}}>证件类型</div>
                        <div style={{flex:'1'}}>
                            <Picker 
                                data={this.state.mCards}  
                                cols={1} 
                                value={this.state.paperType}
                                onOk={value => this.setState({paperType:value})}>
                                <List.Item arrow="horizontal"></List.Item>
                            </Picker>
                        </div>
                    </Flex>
                    {/*证件号码*/}
                    <Flex wrap='wrap' className="search-input-item" style={{marginTop:'3.8vw'}}>
                        <div className="search-input-item-title" style={{width: '34vw'}}>证件号码</div>
                        <div style={{flex:'1'}}>
                            <InputItem
                                type="text"
                                placeholder="请填写"
                                maxLength={18}
                                value={this.state.paperNum}
                                onChange={value => {
                                    if (inputReg.test(value) || '' === value)
                                        this.setState({paperNum:value})
                                }}></InputItem>
                        </div>
                    </Flex>
                    {/*预约号*/}
                    <Flex wrap='wrap' className="search-input-item" style={{marginTop:'3.8vw'}}>
                        <div className="search-input-item-title"  style={{width: '34vw'}}>预约号</div>
                        <div style={{flex:'1'}}>
                            <InputItem
                                type="number"
                                placeholder="请填写"
                                maxLength={18}
                                value={this.state.orderNum}
                                onChange={value => this.setState({orderNum:value})}></InputItem>
                        </div>
                    </Flex>
                    {/*查询按钮*/}
                    <div className="search-confirm-btn" onClick={this.handleSearch}>查询</div>
                </div>

                {/*底部logo*/}
                <div className="cz-logo-relative" style={{marginTop:'41vw'}}></div>
            </div>
        );
    }
}

export default withRouter(ReturnSearch) 


