
const prefix = window._config.prefix;
/*==========Select==========*/
//获取日期信息
export const api_select_days = `${prefix}/reservation/getDays`;
//获取门票信息
export const api_select_tickets = `${prefix}/reservation/getTicketType`;
//获取证件信息
export const api_select_cards = `${prefix}/reservation/getPaperType`;
//获取个人或团队预约信息
export const api_select_reserve = `${prefix}/reservation/searchMesForInter`;
//获取退票信息
export const api_select_return = `${prefix}/reservation/searchDataForBack`;

/*==========Insert==========*/
//保存个人预约信息
export const api_insert_person = `${prefix}/reservation/saveBookingInfo`;
//保存团队预约信息
export const api_insert_team = `${prefix}/reservation/saveGroupTicket`;

/*==========Delete==========*/
//删除一张门票
export const api_delete_ticket = `${prefix}/reservation/refund`;


