import React, { Component } from 'react'
import {Flex, List, Picker, Toast} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import {ticketNum} from '../DataForm'
import DateTag from './DateTag'
import './style-step-1.css'


//Step-->个人预约Step1
class Step1 extends Component{

    //设置所需的State
    constructor(props) {
        super(props);
        this.state = {
            mNum: props.mNum,
            mClass: props.mClass
        };
    } 

    //处理下一步动作
    toNextStep = () => {
        //1. 先判断内容是否填写完整
        if (null == this.props.mDate){
            Toast.fail("请选择日期", 1.5);
            return;
        }
        if (null == this.state.mNum){
            Toast.fail("请选择门票数量", 1.5);
            return;
        }
        if (null == this.state.mClass){
            Toast.fail("请选择门票类型", 1.5);
            return;
        }
        //2. 内容完整后，保存state，进入step2
        this.props.handleStep1(this.state);
    }

    //TODO: 处理日期标签选择
    handleTagSelect = (index) => {
        this.props.handleTagSelect(index);
    }

    render(){

        //TODO: 日期标签列表
        let tagList = this.props.mTags.map((tag, index) => {
            return <DateTag 
                        key = {index}
                        tag = {tag}
                        index = {index}
                        handleTagSelect = {this.handleTagSelect}/>
        }); 

        return (
            <div className='step1-outer-bg'>
                <div className='step1-inner-bg'>
                    {/*参观日期相关*/}
                    <div className='step1-title' style={{marginBottom:'3.7vw'}}>参观日期</div> 
                    <Flex wrap="wrap">
                        {
                            tagList
                        }
                    </Flex>
                    {/*预约说明相关*/}
                    <div className='step1-title' style={{marginTop:'3.2vw'}}>预约说明<span>每次只限5张票</span></div>
                    {/*门票预约相关*/}
                    <div className='step1-title' style={{marginTop:'6.5vw', marginBottom:'2vw'}}>门票预约</div>
                    <List>
                        <Picker 
                            data={ticketNum}  
                            cols={1} 
                            value={this.state.mNum}
                            onOk={value => {this.setState({mNum: value})}}>
                            <List.Item>门票数量</List.Item>
                        </Picker>
                        <Picker 
                            data={this.props.mTicketClass}
                            cols={1} 
                            value={this.state.mClass}
                            onOk={value => {this.setState({mClass: value})}}>
                            <List.Item>门票类型</List.Item>
                        </Picker>
                        <List.Item extra={0}>票价</List.Item>
                        <List.Item extra={'无'}>说明</List.Item>
                    </List>
                </div>

                {/*下一步*/}
                <div className='step1-bottom-btn' onClick={this.toNextStep}>下一步</div> 
            </div>
        );
    }
}

export default withRouter(Step1) 


