import React, { Component } from 'react'
import {Flex, List, InputItem, Picker, DatePicker, Switch, Toast} from 'antd-mobile'
import {createForm} from 'rc-form'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'
import {teamClass, getSimpleDate} from '../DataForm'
import {api_insert_team} from '../Api.js'
import './style-team-reserve.css'

//FIXME: 最小日期（暂时不设置最大日期）-->第二天00:00
const minDate = new Date();
minDate.setDate(minDate.getDate() + 1);
minDate.setHours(0);
minDate.setMinutes(0);
minDate.setSeconds(0);
minDate.setMilliseconds(0);

//Widgets-->自定义的switch
let SwitchExample = (props) => {
    const { getFieldProps } = props.form;
    return (
        <List className="team-list team-list-check" style={{margin: '0 4vw'}}>
            <List.Item className="team-check-box"
                extra={<Switch
                    {...getFieldProps('Switch', {
                        initialValue: false,
                        valuePropName: 'checked',
                    })}
                    color="#75abed"
                    onClick={(checked) => { 
                        //FIXME: 为什么要取非
                        props.handleSwitch(!props.form.getFieldValue('Switch'));
                    }}
                />}>
                是否需要讲解
                <List.Item.Brief>
                    讲解收费标准:<br/>
                    1-20人收取讲解费人民币30元<br/>
                    21-50人收取讲解费人民币50元
                </List.Item.Brief>
            </List.Item>
        </List>
    );
};
SwitchExample = createForm()(SwitchExample);

//Module-->团队预约
class TeamReserve extends Component{

    //初始化State
    constructor(props){
        super(props);
        this.state = {
            teamType: null,
            name: '',
            organizer: '',
            phone: '',
            num: '',
            visitTime: null,
            isinterpretation : false,
            note: '',
        };
    }

    //处理“Switch”(0:否 1:是)
    handleSwitch = (value) => {
        this.setState({
            isinterpretation: value? 1 : 0
        });
    }

    //处理“重新填写”
    handleClear = () => {
        this.setState({
            teamType: null,
            name: '',
            organizer: '',
            phone: '',
            num: '',
            visitTime: null,
            isinterpretation : false,
            note: '',
        });
    }

    //处理“确认预约”
    handleConfirm = () => {
        //1. 是否填写完整
        if (null == this.state.teamType || '' === this.state.name || 
        '' === this.state.organizer || '' === this.state.phone || 
        '' === this.state.num || null === this.state.visitTime ||
        '' ===this.state.note){
            Toast.fail("请填写完整信息", 1.5);
            return;
        }
        //2. 上传团队信息成功后，转至成功界面
        fetch(api_insert_team, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                data: [{
                    teamType: this.state.teamType[0],
                    name: this.state.name,
                    organizer: this.state.organizer,
                    phone: this.state.phone,
                    num: this.state.num,
                    visitTime: getSimpleDate(this.state.visitTime),//TODO: 预约时间要做处理
                    isinterpretation: this.state.isinterpretation,
                    note: this.state.note,
                    address: ''//FIXME: 暂时传空
                }]
            })
        })
            .then((response) => {
                if (response.status >= 200
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data
            })
            .then((data) => {
                //console.log(data);
                this.props.history.replace(
                    `${window._config.path}/team-success`,
                    {
                        num: data,
                    }
                );
            })
    }

    render(){
        return (
            <div style={{background:'white'}}>

                {/*设置头*/}
                <Helmet><title>团队预约</title></Helmet>
                <List className="team-list" style={{margin: '4vw 4vw 0'}}>
                    {/*门票类型*/}
                    <Picker 
                        data={teamClass}  
                        cols={1} 
                        value={this.state.teamType}
                        onOk={value => {this.setState({teamType: value})}}>
                        <List.Item arrow="horizontal">团队类型</List.Item>
                    </Picker>
                    
                    {/*团队名称*/}
                    <InputItem
                        maxLength={18}
                        value={this.state.name}
                        onChange={value => {this.setState({name: value})}}>团队名称</InputItem>
                    
                    {/*联系人*/}
                    <InputItem
                        maxLength={4}
                        value={this.state.organizer}
                        onChange={value => {this.setState({organizer: value})}}>联系人</InputItem>
                    
                    {/*联系号码*/}
                    <InputItem
                        type='phone'
                        value={this.state.phone}
                        onChange={value => {this.setState({phone: value})}}>联系号码</InputItem>

                    {/*参观人数*/}
                    <InputItem
                        type='number'
                        maxLength={2}
                        value={this.state.num}
                        onChange={value => {this.setState({num: value})}}>参观人数</InputItem>
                </List>
                
                {/*参观时间*/}
                <List className="team-list team-list-time"  style={{margin: '0 4vw'}}>      
                    <DatePicker
                        minDate={minDate}
                        value={this.state.visitTime}
                        onChange={date => this.setState({visitTime: date})}>
                        <List.Item arrow="horizontal">
                            参观时间
                            <List.Item.Brief>预约从当天时间的后一天开始</List.Item.Brief>
                        </List.Item>
                    </DatePicker>
                </List>

                <div className='team-check-box-hint'>
                注：我馆开放时间为每日9:00-17:00(16:00停止入场)，周一闭馆</div>

                {/*是否需要讲解*/}
                <SwitchExample handleSwitch={this.handleSwitch}/>

                {/*备注*/}
                <div className="team-textarea-title">备注：</div>
                <div className="team-textarea-control">
                    <textarea 
                        className="team-textarea" 
                        rows="5" 
                        value={this.state.note}
                        onChange={event => {
                            this.setState({note: event.target.value});
                        }}/>
                </div>

                {/*底部按钮组*/}
                <Flex className='team-bottom'>
                    <div className='team-bottom-confirm' onClick={this.handleConfirm}>确认预约</div>
                    <div className='team-bottom-clear' onClick={this.handleClear}>重新填写</div>
                </Flex>
            </div>
        );
    }
}

export default withRouter(TeamReserve)


