import React, { Component } from 'react'
import {Flex, Toast} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import InputInfo from './InputInfo'
import './style-step-2.css'
import { validateIdCard } from '../DataForm';

//Step-->个人预约Step2
class Step2 extends Component{

    //设置所需的State
    constructor(props){
        super(props);
        this.state = {
            mInputs: this.props.mInputs
        };
    }

    //根据选择数量，生成指定长度的数组
    componentWillMount(){
        if (0 === this.state.mInputs.length){
            let inputs = [];
            for (var i = 0; i <= this.props.mNum[0]; i++)
                inputs.push({
                    ticketName: this.props.ticketName,
                    areaType: null,
                    cardType: null,
                    cardNum: '',
                    name: ''
                });
            this.setState({
                mInputs: inputs
            });
        }
    }

    //处理上一步动作
    toPrevStep = () => {
        this.props.toPrevStep(2);
    }

    //处理下一步动作
    toNextStep = () => {
        //1. 判断是否填写完整
        for (var i = 0; i < this.state.mInputs.length; i++){
            let inputs = this.state.mInputs[i];
            if ('' === inputs.cardNum || '' === inputs.name || null == inputs.areaType || null == inputs.cardType ){
                Toast.fail("请填写完整信息", 1.5);
                return;
            }
            //FIXME: 目前仅处理身份证件，并且默认0 == value时是身份证
            if (0 == inputs.cardType[0] && !validateIdCard(inputs.cardNum)){
                Toast.fail("请输入正确的身份证号", 1.5);
                return;
            }
        }
        //2. 内容完整后，修改state，并进入step3
        this.props.handleStep2(this.state.mInputs);
    }

    //FIXME: 处理每个输入框的改变（不需要setState即可修改state？）
    setInputsData = (inputs, index) =>{
        let inputsArr = this.state.mInputs;
        inputsArr[index] = inputs;
    }

    render(){

        //预约门票列表
        let inputList = this.state.mInputs.map((inputs, index) => {
            return <InputInfo 
                        key = {index} 
                        inputs = {inputs} 
                        index = {index}
                        cardClass = {this.props.mCardClass}
                        setInputsData = {this.setInputsData}/>
        });

        return (
            <div style={{background:'#ffffff'}}>
                {/*TODO: 入馆时间*/}
                <div className='step2-date'>入馆时间：{this.props.mDate}</div>
                {/*填写信息*/}
                <Flex direction='column' className='step2-input-list'>
                    {
                        inputList
                    }
                </Flex>
                {/*固定底部的“上一步”和“下一步”按钮*/}
                <Flex className='step2-bottom'>
                    <div className='step2-bottom-back' onClick={this.toPrevStep}>上一步</div>
                    <div className='step2-bottom-next' onClick={this.toNextStep}>下一步</div>
                </Flex>
            </div>
        );
    }
}

export default withRouter(Step2) 


