import React, { Component } from 'react';
import {List} from 'antd-mobile';

//Widgets-->显示个人信息的小卡片
class PersonItem extends Component{

    /*{
        "printFlag": "0",
        "area": "安徽",
        "ticketName": "成人票",
        "ordernum": "1515385912878",
        "name": "测试",
        "date": "2018-01-09",
        "cardType": "护照",
        "cardNum": "222222222222222222"
    }*/
    render(){

        //获取个人预约状态
        let status = (flag) => {
            switch(flag){
                case "0": return '未取票'
                case "1": return '已取票'
                case "2": return '已退票'
                case "3": return '废票'
            }
        }

        return (
            <div className="card-outer">
                <List className="card-inner">
                    <List.Item extra={this.props.datas.ordernum}>预约号</List.Item>
                    <List.Item extra={this.props.datas.ticketName}>门票类型</List.Item>
                    <List.Item extra={this.props.datas.name}>姓名</List.Item>
                    <List.Item extra={this.props.datas.cardType}>证件类型</List.Item>
                    <List.Item extra={this.props.datas.cardNum}>证件号</List.Item>
                    <List.Item extra={this.props.datas.area}>地区</List.Item>
                    <List.Item extra={this.props.datas.date}>入馆时间</List.Item>
                    <List.Item extra={status(this.props.datas.printFlag)}>状态</List.Item>
                </List>
            </div>
        );
    }
}

export default PersonItem


