import React, { Component } from 'react'
import {List, Picker, InputItem} from 'antd-mobile'
import {provList} from '../DataForm'

const inputReg = new RegExp("^[0-9a-zA-Z]+$");
//Widgets-->个人预约信息输入
class InputInfo extends Component{

    /*{
        ticketName: this.props.mTicketClass,
        areaType: null,
        cardType: [0],
        cardNum: '',
        name: ''
    }*/
    constructor(props) {
        super(props);
        this.state = this.props.inputs;
    }

    render(){
        return (
            <div className='step2-input-outer'>
            <List className="step2-input-inner">
                {/*门票类型*/}
                <List.Item extra={this.state.ticketName}>门票类型</List.Item>
                {/*证件类型*/}
                <Picker 
                    data={this.props.cardClass}  
                    cols={1}
                    value={this.state.cardType}
                    onOk={value => {
                            this.setState({
                                cardType: value
                            }, () => {
                                this.props.setInputsData(this.state, this.props.index)
                            });
                        }}>
                    <List.Item arrow="horizontal">证件类型</List.Item>
                </Picker>
                {/*证件号*/}
                <InputItem
                    placeholder='请填写证件号'
                    maxLength={18}
                    value={this.state.cardNum}
                    onChange={value => {
                            if (inputReg.test(value) || '' === value){
                                this.setState({
                                    cardNum: value
                                }, () => {
                                    this.props.setInputsData(this.state, this.props.index)
                                })
                            }
                        }}>证件号</InputItem>
                {/*地区*/}
                <Picker 
                    data={provList}  
                    cols={1}
                    value={this.state.areaType}
                    onOk={value => {
                            this.setState({
                                areaType: value
                            }, () => {
                                this.props.setInputsData(this.state, this.props.index)
                            })
                        }}>
                    <List.Item arrow="horizontal">地区</List.Item>
                </Picker>
                {/*姓名*/}
                <InputItem
                    placeholder='请填写姓名'
                    maxLength={4}
                    value={this.state.name}
                    onChange={value => {
                            this.setState({
                                name: value
                            }, () => {
                                this.props.setInputsData(this.state, this.props.index)
                            })
                        }}>姓名</InputItem>
            </List>
            </div>
        );
    }
}

export default InputInfo


