import React, { Component } from 'react'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'
import {Toast} from 'antd-mobile'
import {api_select_days, api_select_cards, api_select_tickets} from '../Api'
import Step1 from '../Person/Step1'
import Step2 from '../Person/Step2'
import Step3 from '../Person/Step3'

//Moudle-->个人预约界面
class PersonReserve extends Component{

    //设置预约界面需要维持的state
    constructor(props){
        super(props);
        this.state = {
            mStep: 1,
            mDate: null,
            mNum: null,
            mClass: null,
            mInputs: [],
            mTags: [],
            mTicketClass: [],
            mCardClass: [],
        }
    }

    //TODO: 如何处理错误数据
    async componentDidMount(){

        //获得10天的门票
        fetch(api_select_days, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then((response) => {
                if (response.status >= 200 
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data;
            })
            .then((data) => {
                //TODO: 接口是否可以直接生成selected参数
                for (let i = 0; i < data.length; i++)
                    data[i].selected = 0;
                this.setState({
                    mTags: data
                });
            })
            .catch((error) => {
                console.log("error-->"+error);
            });

        //获得门票类型
        fetch(api_select_tickets, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then((response) => {
                if (response.status >= 200 
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data;
            })
            .then((data) => {
                //TODO: 接口是否可以直接生成该格式
                let ticketClass = [];
                for (var i = 0; i < data.length; i++)
                    ticketClass.push({
                        label: data[i].NAME,
                        value: data[i].ID
                    });
                this.setState({
                    mTicketClass: ticketClass
                });
            })
            .catch((error) => {
                console.log("error-->"+error);
            });

        //获得证件类型
        fetch(api_select_cards, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
        })
            .then((response) => {
                if (response.status >= 200
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data;
            })
            .then((data) => {
                //TODO: 接口是否可以直接生成该格式
                let cardClass = [];
                for (var i = 0; i < data.length; i++)
                    cardClass.push({
                        label: data[i].text,
                        value: data[i].value
                    });
                this.setState({
                    mCardClass: cardClass
                });
            })
            .catch((error) => {
                console.log("error-->"+error);
            });
    }

    //处理日期的修改
    handleTagSelect = (index) => {
        let tagArr = this.state.mTags;
        for (let i = 0; i < tagArr.length; i++){
            if (i === index){
                tagArr[i].selected = 1;
                this.setState({
                    mDate: tagArr[i].date   //使用选中日期
                });
            } else {
                tagArr[i].selected = 0;
            }
        }
        this.setState({
            mTags: tagArr
        });
    }

    //处理Step1数据的修改
    handleStep1 = (dataItem) => {
        this.setState({
            mStep: 2,
            mNum: dataItem.mNum,
            mClass: dataItem.mClass
        });
    }

    //处理Step2数据的修改
    handleStep2 = (inputs) => {
        this.setState({
            mStep: 3,
            mInputs: inputs
        });
    }

    //上一页，若2->1，则要清空mInputs
    toPrevStep = (index) => {
        if (2 === index)
            this.setState({
                mInputs: []
            }, () => {
                this.setState({
                    mStep: this.state.mStep - 1
                })
            });
        else 
            this.setState({
                mStep: this.state.mStep - 1
            });
    }

    //更新Module State数据
    setModuleState = (dataItem) => {
        dataItem.mStep++;   //下一步
        this.setState(dataItem);
    }

    //内部包含3个Step步骤页
    render(){
        //Step1
        let step1 = (1 === this.state.mStep) ? (
            <Step1  
                mNum = {this.state.mNum}
                mClass = {this.state.mClass}
                mDate = {this.state.mDate}
                mTags = {this.state.mTags}
                mTicketClass = {this.state.mTicketClass}
                handleStep1 = {this.handleStep1}
                handleTagSelect = {this.handleTagSelect}/>
        ) : null;
        //Step2
        let getTicketStr = (tickets, selected) => {
            for (let i = 0; i < tickets.length; i++){
                if (tickets[i].value == selected)
                    return tickets[i].label;
            }
        };
        let step2 = (2 === this.state.mStep) ? (
            <Step2
                mNum = {this.state.mNum}
                mDate = {this.state.mDate}
                mInputs = {this.state.mInputs}
                mCardClass = {this.state.mCardClass}
                ticketName = {getTicketStr(this.state.mTicketClass, this.state.mClass)}
                handleStep2 = {this.handleStep2}
                toPrevStep = {this.toPrevStep}/>
        ) : null;
        //Step3
        let step3 = (3 === this.state.mStep) ? (
            <Step3
                moduleData = {this.state}
                toPrevStep = {this.toPrevStep}/>
        ) : null;

        return (
            <div>
                <Helmet><title>个人预约</title></Helmet>
                {
                    step1
                }
                {
                    step2
                }
                {
                    step3
                }
            </div>
        );
    }
}

export default withRouter(PersonReserve) 


