import React, { Component } from 'react';
import {Flex} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'

const teamSuccess = '/static/visit/cz_team_success.png'
//Module-->团队预约成功后的界面
class TeamSuccess extends Component{

    //处理“查看预约”
    handleSearch = () => {
        //跳转至搜索结果界面
        this.props.history.push(
            `${window._config.path}/result`,
            {
                type: 1,
                num: this.props.location.state.num
            },
        );
    }

    render(){

        //背景
        let bgStyle = {
            width: '100%',
            height: '100vh',
            background:`url(${teamSuccess}) top  no-repeat / contain`,
        };

        return (
            <div style={{background:'#ffffff'}}>
            <Helmet><title>团队预约申请成功</title></Helmet>
            <div style={bgStyle}>

                <div className='success-bottom'>
                    {/*动态显示预约号 */}
                    <div className='success-number'>你的预约号: {this.props.location.state.num}</div>
                    {/*提示内容框*/}
                    <div className='success-card'>
                        <div className='success-card-title'>温馨提示:</div>
                        <div className='success-card-detail'>请牢记您的预约号，并于1-2个工作日后电话咨询或网上查询预约信息是否通过审核，以免错过参观！</div>
                        <Flex>
                            <img className='success-wechat' src='/static/visit/cz_wechat.png'/>
                            <div className='success-card-hint'>长按二维码关注常州博物馆公众号及时查看预约状态</div>
                        </Flex>
                    </div>
                    {/*底部按钮*/}
                    <div className='success-btn' onClick={this.handleSearch}>查看预约</div>
                </div>

            </div>
            </div>
        );
    }
}

export default withRouter(TeamSuccess) 


